const groupBy = (list, keyGetter) => {
    const map = new Map();
    if(list !== undefined){
        list.forEach((item) => {
            const key = keyGetter(item);
            const collection = map.get(key);
            if (!collection) {
                map.set(key, [item]);
            } else {
                collection.push(item);
            }
        });
    }
    return map;
  
}

export default groupBy

