import React, { Component } from "react";
import { Form } from "react-bootstrap";

class Dropdown extends Component {
    constructor(props) {
        super(props);
       
        // state initializing
        this.state = {
            currentValue: '',
            class: 'mb-3 form-select',
            options: [],
         };
  
    }
  
    componentDidMount(){ 
        // initializing if current value from passing parent 
        this.setState({
            currentValue:this.props.currentValue
        }) 

    }

  
    render() {

       return (
        <Form.Select 
            className={`${this.props.class} form-control`}
            onChange={this.props.handleChange}      
            value={this.state.currentValue} 
            data-id={this.props.type} 
        >  
            {         
                this.props.label && (<option key={0} value="-1"> {this.props.label} </option>)
            }
            {
                this.props.data && this.props.data.map((data) => (
                    <option key={data.value} value={data.value}> {data.label} </option>
                ))
            }   
     </Form.Select>

      );
    }
}

export default Dropdown;

  