import http from "../interceptors/http-common";

class ResultsDataService {

  getResults(electionId, roundId, departmentId, cityId, circoId, countyId, pollingStationNumber, candidaName,gpsPollingStation) {

    var url = `election/results?electionId=${electionId}&round=${roundId}&departmentId=${departmentId}`;

    if (cityId !== null && cityId !== "" && cityId !== undefined) {
      url += "&cityId=" + cityId;
    }

    if (circoId !== null && circoId !== "" && circoId !== undefined) {
      url += "&circoId=" + circoId;
    }

    if (countyId !== null && countyId !== "" && countyId !== undefined) {
      url += "&countyId=" + countyId;
    }

    if (pollingStationNumber !== null && pollingStationNumber !== "" && pollingStationNumber !== undefined) {
      url += "&pollingStationNumber=" + pollingStationNumber;
    }

    if (candidaName !== null && candidaName !== "" && candidaName !== undefined) {
      url += "&candidateName=" + candidaName;
    } 
    
    if (gpsPollingStation !== null && gpsPollingStation !== "" && gpsPollingStation !== undefined) {
      url += "&gpsPollingStation=" + gpsPollingStation;
    }

    return http.get(url);
  }


  getSinglePopup(departmentID,cityId,pollingStationNumber,pollingStationGPS){
    var url = `election/results?departmentID=${departmentID}&cityId=${cityId}&pollingStationNumber=${pollingStationNumber}&pollingStationGPS=${pollingStationGPS}`;

    return http.get(url);
  }

}
export default new ResultsDataService();
