 import  React, { useEffect,useRef,useState } from "react";
import { Form , Button, Col, Container, Row, Spinner } from "react-bootstrap";
import { useDispatch } from "react-redux";

import Dropdown from "../elements/form/Dropdown";
import DropdownSearch from 'react-select';


// data request services
import ElectionDataService from "../../services/election.service";
import RoundsDataService from "../../services/rounds.service";
import AreasDataService from "../../services/areas.service";
import DepartmentsDataService from "../../services/departments.service";
import CircosDataService from "../../services/circos.service";
import CountyDataService from "../../services/county.service";
import CitiesDataService from "../../services/cities.service"
import ResultsDataService from "../../services/results.service"

//redux actions calling
import { setLoading, setResults, } from "../../redux/actions/resultsActions";

function Search(props) {
    const dispatch = useDispatch();
    const cityRef = useRef();

    const [getCountryId,setCountryId] = useState("FR");
    const [getElectionId,setElectionId] = useState(null);
    const [getRoundsId,setRoundsId ] = useState(null);
    const [getAreasId,setAreasId] = useState(null);
    const [getDepartmentsId,setDepartmentsId] = useState(null);
    const [getCircoId,setCircoId] = useState(null);
    const [getCountyId,setCountyId] = useState(null);
    const [getCityId,setCityId] = useState([]);
    const [getPollingStation,setPollingStation] = useState(null);
    const [getCandidate,setCandidate] = useState(null);
    const [isLoading,setIsLoading] = useState(false);


    // data inserting  
    const [electionData,setElectionData] = useState([]);
    const [roundsData,setRoundsData] = useState([]);
    const [areasData,setAreasData] = useState([]);
    const [departmentsData,setDepartmentsData] = useState([]);
    const [circoData,setCircoData] = useState([]);
    const [countyData,setCountyData] = useState([]);
    const [cityData,setCityData] = useState([]);

    const fetchElections = async ()=>{
        try {
            const response = await  ElectionDataService.getAll();
            if(response.status === 200){
                setElectionData(response.data.data.map((data) => (
                    {
                        value:data.electionId,
                        label:data.electionName,
                    }
                )) )
            }
        } catch (error) {
            console.log(error);
        }

    }
 
    const fetchAreas= async ()=>{
        try {
            const response = await  AreasDataService.getAreaLists(getCountryId);
            if(response.status === 200){
                setAreasData(response.data.data.map((data) => (
                    {
                        value:data.region[0].code,
                        label:data.region[0].name,
                    }
                )) )
            }
        } catch (error) {
            console.log(error);
        }

    }

    
    useEffect(()=>{
        fetchElections();
        fetchAreas();
        setCountryId("FR");
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);


        
   const handleChange = async (event)=> {
    const TYPE = event.target.dataset.id;
    const VALUE = event.target.value;
    var citiesRes = null;
 
    switch(TYPE){
    case 'ELECTION' :
        
        //when election change it retrieves rounds by sending election ID 
        if(VALUE !== null || VALUE !== '' ){ 
            const roundsRes = await  RoundsDataService.getRoundsByElectionId(VALUE);
            if(roundsRes.status === 200){
                setRoundsData(roundsRes.data.data.map((data) => (
                    {
                        value:data.round.id,
                        label:data.round.collectionName,
                    }  
                ))) 
            }  
        }

        //passing current value to state
        setElectionId(VALUE);
        break;
    case 'ROUNDS' :

        // when change rounds get its value
        setRoundsId(VALUE);
    
        break;

    case 'AREAS' :  

        // resetting other dropdown default
        setDepartmentsId('-1');
        setCircoId('-1');
        setCountyId('-1');

        //when areas change it retrieves rounds by sending election ID  
        if(getAreasId !== null || getAreasId !== '' ){ 
            const departmentsRes = await  DepartmentsDataService.getLists(getCountryId,VALUE);
            if(departmentsRes.status === 200){
                setDepartmentsData(departmentsRes.data.data.map((data) => (
                    {
                        value:data.department[0].code,
                        label:data.department[0].name,
                    }  
                ))) 
            }  
        }

        // when change rounds get its value
       setAreasId(VALUE);

    
        break;
    case 'DEPARTMENTS' :  
         // resetting other dropdown default
        setCircoId('-1');
        setCountyId('-1');
 
        if(cityRef.current){
            cityRef.current.clearValue()
        }

        //when areas change it retrieves rounds by sending election ID  
        if((getAreasId !== null && VALUE !== null) || (getAreasId !== ''  && VALUE !=='') ){ 
            
            //retrieves circos
            const circosRes = await CircosDataService.getLists(getCountryId,getAreasId,VALUE);
            if(circosRes.status === 200){
                setCircoData(circosRes.data.data.map((data) => (
                    {
                        value:data.circo[0].code,
                        label:data.circo[0].code,
                    }  
                ))) 
            }  

            //retrieves cities 
            citiesRes = await CitiesDataService.getLists(getCountryId,getAreasId,VALUE);
            if(citiesRes.status === 200){
                setCityData(citiesRes.data.data.map((data) => (
                    {
                        value:data.list[0].cityCodeLong,
                        label:data.list[0].cityName,
                    }  
                ))) 
                
                setCityId(citiesRes.data.data.map((data) => (
                        data.list[0].cityCodeLong
                ))) 

            }       


        } 

        // when change rounds get its value
        setDepartmentsId(VALUE);  

        break; 
    case 'CIRCOS' :  

        // resetting other dropdown default 
        setCountyId('-1');
        if(cityRef.current){
            cityRef.current.clearValue()
        }
        //when areas change it retrieves rounds by sending election ID  
        if((getAreasId !== null && VALUE !== null) || (getAreasId !== ''  && VALUE !=='') ){ 
            const countyRes = await CountyDataService.getLists(getCountryId,getAreasId,getDepartmentsId,VALUE);
            if(countyRes.status === 200){
                setCountyData(countyRes.data.data.map((data) => (
                        {
                        value:data.county[0].code,
                        label:data.county[0].name,
                    }   
                ))) 
            }  
        }

        //retrieves cities 
        citiesRes = await CitiesDataService.getLists(getCountryId,getAreasId,getDepartmentsId,VALUE);
        if(citiesRes.status === 200){
            setCityData(citiesRes.data.data.map((data) => (
                {
                    value:data.list[0].cityCodeLong,
                    label:data.list[0].cityName,
                }  
            ))) 

            setCityId(citiesRes.data.data.map((data) => (
                    data.list[0].cityCodeLong
            ))) 
        }  
        
        // when change rounds get its value
        setCircoId(VALUE);    

    
        break;
    case 'COUNTY' :  
    
        // resetting other dropdown default 
        if(cityRef.current){
            cityRef.current.clearValue()
        }

        //retrieves cities 
         citiesRes = await CitiesDataService.getLists(getCountryId,getAreasId,getDepartmentsId,getCircoId,VALUE);
         if(citiesRes.status === 200){
            setCityData(citiesRes.data.data.map((data) => (
                {
                    value:data.list[0].cityCodeLong,
                    label:data.list[0].cityName,
                }  
            )))

            setCityId(citiesRes.data.data.map((data) => (
                    data.list[0].cityCodeLong
            ))) 
        }  
        
        // when change rounds get its value
        setCountyId(VALUE);    
 
        break;
    default: 
        break;

    }
    
    }  


    const setCityValue = (e)=> {
        if( e == null){
           return setCityId([]) 
        }
        return setCityId([e.value])  
    } 
    
    const searchData = async(event) =>{
        
        event.preventDefault()
        setIsLoading(true);
        dispatch(setLoading(true));
        try {
            await ResultsDataService.getResults(
                getElectionId !== "-1" ? getElectionId : "",
                getRoundsId !== "-1" ? getRoundsId : "",
                getDepartmentsId !== "-1" ? getDepartmentsId : "",
                getCityId !== "-1" ? getCityId.toString() : [],
                getCircoId !== "-1" ? getCircoId : "",
                getCountyId !== "-1" ? getCountyId : "",
                getPollingStation !== "" ? getPollingStation : "",
                getCandidate !== "" ? getCandidate : "",
            ).then(res=>{
                dispatch(setResults(res.data)); 
                setIsLoading(false)
            });
            } catch (error) {
            console.log("await error", error);
          }

    }
    

    return(
    <>
      
        <Form  className="py-3" onSubmit={searchData}>
            {console.log(getCityId.toString())}
       <Container fluid className="p-0">
                <Row>
                    <Col xs={12} md={4}>
                        <Dropdown 
                            data={electionData}
                            label={"-- Election --"} 
                            handleChange={handleChange}
                            type={'ELECTION'}
                            class={'mb-3'} 
                         />
                    </Col> 

                    <Col xs={12} md={4}>
                        <Dropdown 
                            data={roundsData}
                            label={"-- ROUNDS --"} 
                            handleChange={handleChange}
                            type={'ROUNDS'}
                            class={'mb-3'} 
                         /> 
                    </Col> 
                    <Col xs={12} md={4}>
                      <Dropdown 
                            data={areasData}
                            label={"-- AREAS --"} 
                            handleChange={handleChange}
                            type={'AREAS'}
                            class={'mb-3'}
                        /> 
                    </Col>  
                </Row> 
                <Row> 
     
                    <Col xs={12} md={4}>
                        {
                           <Dropdown 
                                data={departmentsData}
                                label={"-- DEPARTMENTS --"} 
                                handleChange={handleChange}
                                type={'DEPARTMENTS'}
                                class={'mb-3'}
                            />
                        }  

                    </Col>  
                    <Col xs={12} md={4}>
                      {
                            getDepartmentsId !== null  &&
                            getDepartmentsId !== "-1" &&
                            getAreasId !== null &&
                            getAreasId !== "-1" &&
                            <Dropdown 
                                data={circoData}
                                label={"-- CIRCOS --"} 
                                handleChange={handleChange}
                                type={'CIRCOS'}
                                class={'mb-3'}
                            />
                        }  

                    </Col>  
                    
                    
                    <Col xs={12} md={4}>
                       {
                            getDepartmentsId !== null && 
                            getDepartmentsId !== "-1" &&
                            getAreasId !== null && 
                            getAreasId !== "-1" &&
                            getCircoId !== null && 
                            getCircoId !== "-1" &&
                            <Dropdown 
                                data={countyData}
                                label={"-- COUNTYS --"} 
                                handleChange={handleChange}
                                type={'COUNTY'}
                                class={'mb-3'}
                            />
                            

                        } 
                     </Col>    
                </Row>  
                <Row>
                   
                        { 
                        
                        

                            (getAreasId !== null && 
                            getAreasId !== "-1"  &&
                            getDepartmentsId !== null  &&
                            getDepartmentsId !== "-1" ) &&
                            <Col xs={12} md={4}>
                                <DropdownSearch 
                                options={cityData} 
                                className="mb-3"  
                                onChange={setCityValue} 
                                isClearable
                                isSearchable
                                ref={cityRef}
                                /> 
                            </Col>
                         
                        }  
                   
                    <Col>
                        <Form.Group className="mb-3" controlId="formPolling">
                        <Form.Control type="text" placeholder="ID polling stations"  onChange={(event)=>setPollingStation(event.target.value)}/> 
                        </Form.Group>
                    </Col> 
                    <Col>
                        <Form.Group className="mb-3" controlId="formCandidate">
                            <Form.Control type="text" placeholder="Candidate" onChange={(event)=>setCandidate(event.target.value)}  /> 
                        </Form.Group>
                    </Col>  
                </Row>   
                 <Button variant="secondary" className="text-white" type="submit"  disabled={
                               (
                                    getElectionId !== null && 
                                    getElectionId !== "-1" && 
                                    getRoundsId !== null && 
                                    getRoundsId !== "-1" && 
                                    getAreasId !== null &&  
                                    getAreasId !== "-1" &&  
                                    getDepartmentsId !== null &&
                                    getDepartmentsId !== "-1"
                                ) === false ? true: false
                            }>
                { isLoading === true ?   
                 <div className="d-flex">
                    <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                    className="mt-1"
                    /> 
                   <p className="pl-2 m-0"> Loading...</p> 
                </div>
                    : "Submit"
                }
                </Button>
        </Container> 
        </Form>
    </>
    )
}
 
export default Search 