import { ActionTypes } from "../constants/action-types";

const initialState = {
    results:[],
    loading:false,
};


export const resultsReducers = (state = initialState,{type,payload}) =>{
    switch (type) {
        case ActionTypes.SET_RESULTS:
            return {
                ...state,
                results:payload,
                loading: false,
            }

        case ActionTypes.IS_LOADING:
            return {
                ...state,
                loading: true,
            }
        default:
            return state
    }
}