
import React, { Component } from "react";
import { connect } from "react-redux";
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import overlayFactory from 'react-bootstrap-table2-overlay';
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import roundTwo from "../../utils/roundTwo";
import groupBy from "../../utils/groupBy";

const NoDataIndication = () => (
  <div className="spinner">
    No data found...
  </div>
);
 
function resultExpress(cell, row) {
  return <>{ roundTwo(row.electionResults.resultExpressP) }</>
}


const columns = [
  {
    dataField: 'electionResults.candidateName',
    text: 'Candidate',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white ',
    headerStyle: {
      width: '250px'
    } 
    }, {
    dataField: 'partyInfo[0].partyName',
    text: 'Party',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
      width: '250px'
    }
  }, {
    dataField: 'electionResults.result',
    text: 'Results',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
        width: '80px'
    }
  }, {
    dataField: 'electionResults.resultExpressP',
    text: '% Results',
    align:'center',
    sort: true,
    formatter: resultExpress,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
        width: '100px'
    }

  }, {
    dataField: 'electionResults.circoCode',
    text: 'Circo',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
        width: '60px'
    }

  }, {
    dataField: 'electionResults.departmentName',
    text: 'Department',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
        width: '120px'
    }

  }, {
    dataField: 'roundCode',
    text: 'Round',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
        width: '70px'
    }

  }, {
    dataField: 'electionResults.cityName',
    text: 'City',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
      width: '200px'
    }

  }, {
    dataField: 'electionResults.pollingStationNumber',
    text: 'Polling Stations',
    align:'center',
    sort: true,
    sortCaret: (order, column) => {
      if (order === "asc") {
        return <i class="caret-4-asc"></i>
      } else if (order === "desc") {
        return <i class="caret-4-desc"></i>
      }
    },
    headerClasses: 'bg-green text-white',
    headerStyle: {
      width: '150px'
    }
  },


];

const customTotal = (from, to, size) => (
  <span className="pl-2 react-bootstrap-table-pagination-total">
    Showing {from} to {to} of {size} Results
  </span>
);

const options = {
  paginationSize: 10,
  pageStartIndex: 0, 
  firstPageText: 'First',
  prePageText: 'Back',
  nextPageText: 'Next',
  lastPageText: 'Last',
  nextPageTitle: 'First page',
  prePageTitle: 'Pre page',
  firstPageTitle: 'Next page',
  lastPageTitle: 'Last page',
  showTotal: true,
  paginationTotalRenderer: customTotal,
  disablePageTitle: true,
  sizePerPageList: [{
    text: '5', value: 5
  }, {
    text: '10', value: 10
  }, {
    text: '20', value: 20
  }, {
    text: '50', value: 50
  }, {
    text: '100', value: 100
  }] // A numeric array is also available. the purpose of above example is custom the text
};



class TableResults extends Component {
  
  render(props) {
      
    const dataSet = this.props.loading === true ? [] : this.props.results && this.props.results.data && this.props.results.data[0] && this.props.results.data[0].election;
    const grouped = groupBy(dataSet, element => element.electionResults.pollingStationNumber);
 
    var registerSum = 0;
    var voterSum = 0;
    var abstentionSum = 0;

    grouped.forEach(function (elements,key) {
           // eslint-disable-next-line array-callback-return
           elements.map(function(result, index, arr) {
            
            //getting only first record
              if(index === 0){ 
                registerSum += result.electionResults && result.electionResults.register;
                voterSum += result.electionResults && result.electionResults.voter;
                abstentionSum += result.electionResults && result.electionResults.absentation;
              }
              
           });   
    });
   
    return ( 
      <>
 
      { dataSet !== undefined && dataSet !== [] && this.props.loading === false && <div className="text-center border py-2 mb-3">  TOTAL based on area selected <strong>Register</strong>  =  {registerSum} / <strong> Voter </strong>  =  {voterSum} /  <strong>Abstention</strong> =  {abstentionSum}  </div> }
 
        <BootstrapTable
          keyField='id'
          data={dataSet === undefined ? [] : dataSet}
          columns={columns}
          loading={this.props.loading}
          pagination={paginationFactory(options)}
          noDataIndication={() => <NoDataIndication />}
          overlay={overlayFactory({ spinner: true, styles: { overlay: (base) => ({ ...base, background: 'rgba(0,0,0,0.5)' }) } })}
          classes=' table-responsive-sm table-responsive-md table-responsive-lg table-responsive-xl'
           />
      </>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    results: state.resultsData.results,
    loading: state.resultsData.loading,
  };
};

export default connect(mapStateToProps)(TableResults);
