import http from "../interceptors/http-common";

class  CitiesDataService {
   
  getLists(countryID,areaId,departmentId,circoId,countyId) {

    
    var url = `/country/list-cities?countryId=${countryID}&regionId=${areaId}` ;

    if (departmentId !== null && departmentId !== "" && departmentId !== undefined){
        url += "&departmentId=" + departmentId;
    }
    
    if (circoId !== null && circoId !== "" && circoId !== undefined){
        url += "&circoId=" + circoId;
    }  
    
    if (countyId !== null && countyId !== "" && countyId !== undefined){
        url += "&countyId=" + countyId;
    }
  
    return http.get(url);
    
    // return http.get(`/country/list-cities?countryId=${countryID}&regionId=${areaId}&departmentId=${departmentId}`);
  } 
 
}
export default new  CitiesDataService();
