import http from "../interceptors/http-common";

class  CountyDataService {
   
  getLists(countryID,areaId,departmentId,circoId) {
    return http.get(`/country/list-county?countryId=${countryID}&regionId=${areaId}&departmentId=${departmentId}&circoId=${circoId}`);
  } 
 
}
export default new  CountyDataService();
