
import React, { Component } from "react";
import { connect } from "react-redux";
import { MapContainer, TileLayer, Marker, Popup} from 'react-leaflet'
import MarkerClusterGroup from 'react-leaflet-markercluster';
import L from 'leaflet';

import "leaflet/dist/leaflet.css";
import "react-leaflet-markercluster/dist/styles.min.css"; 

import ResultsDataService from "../../services/results.service"


import markerIconPng from "../../assets/img/vote.png";
import groupBy from "../../utils/groupBy";
import roundTwo from "../../utils/roundTwo";
 
const createClusterCustomIcon = function (cluster) {
    return L.divIcon({
    html: `<span>${cluster.getChildCount()}</span>`,
    className: 'marker-cluster-custom',
    iconSize: L.point(40, 40, true)
    });
  };
 
  const markerIcon = L.icon({
    iconUrl: markerIconPng,
    iconSize: [25, 41], 
    iconAnchor: [12, 41]
  });
  
class MapResult extends Component {
 

    constructor(props) {
        super(props);

        this.state = {
            results: [],
            location: [],
            center: [], 
            popup: [],
            popupLoading:false
        }
    }

    render(props) {
  
        let centerLocation = [48.864716, 2.349014]; //paris
        return ( 
            <MapContainer
             center={centerLocation} 
             style={{ height: '75vh', width: '75wh' }}
             zoom={4}
             maxZoom={18}
             className="mt-5 markercluster-map"
            
            > 

            <TileLayer
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
               <MarkerClusterGroup
                    showCoverageOnHover={false}
                    iconCreateFunction={createClusterCustomIcon}
                > 
                    { 
                         this.props.mapResults && this.props.mapResults.length > 0 && this.props.mapResults.map((element,index) => (
                                <Marker  
                                icon={markerIcon}
                                position={element.coordinates.split(",")} 
                                eventHandlers={{
                                    click: async (e) => { 
                                         this.setState(state => ({
                                                popup: [],
                                                popupLoading: true,
                                            }));
                                         await ResultsDataService.getResults( 
                                            'Presidential_Election_2017',
                                            element.roundCode,
                                            element.data.departmentCode,
                                            element.data.cityCode, 
                                            element.data.circoCode, 
                                            element.data.countyCode, 
                                            element.data.pollingStationNumber,
                                            null, 
                                            element.data.gpsPollingStation, 
                                         ).then(res=>{ 

                                            this.setState(state => ({
                                                popup: [...state.popup, res.data.data[0].election],
                                                popupLoading: false,

                                            }))
                                        });
                                   
                                    },
                                }} > 
                                    <Popup className="text-center" maxWidth={600}>
                                        <h5 className="text-center">{element.data.cityName} - Polling Station {element.data.pollingStationNumber} </h5>
                                        <h6 className="text-center">Register: {element.data.register} Voter: {element.data.voter} Absentation: {element.data.absentation} </h6>
                                        <hr/>  
                                        {this.state.popupLoading && (<div class="d-block m-auto spinner-border"></div>)}
                                        {this.state.popupLoading === false && this.state.popup && this.state.popup.length > 0 && (
                                        <ul className="map-popup-scroll">  
                                            { 
                                                this.state.popup[0].map((element,index) => ( 
                                                    <li key={element.partyInfo[0].lastName+index+element.partyInfo[0].hexColor} id={element.electionResults._id} > 
                                                    <span className="circle-bg" style={{backgroundColor: element.partyInfo[0].hexColor}}></span> 
                                                    {element.partyInfo[0].lastName} ({element.partyInfo[0].partyName}) {roundTwo(element.electionResults.resultExpressP)} % ({element.electionResults.result})
                                                    </li> 
                                                ))
                                            }
                                        </ul> )}
                                    </Popup> 
                                  
                                </Marker>  
                         )) 
                    }  
            </MarkerClusterGroup>;


          </MapContainer> 
        );
    }
}


const mapStateToProps = (state) => {
    let mapLatLong = [];

    if(!state.resultsData.loading){
        const data = state.resultsData.loading === true ? null : state.resultsData.results && 
        state.resultsData.results.data &&
        state.resultsData.results.data[0]&&
        state.resultsData.results.data[0].election&&
        state.resultsData.results.data[0].election;

        if(data !== undefined){ 
            const grouped = groupBy(data, element => element.electionResults.gpsPollingStation);
            console.log(grouped);
            grouped.forEach(function (elements,key) {
                   // eslint-disable-next-line array-callback-return
                   return elements.map(function(result, index, arr) {
                        if(index === 0){ 
                            if(result.electionResults.gpsPollingStation !== undefined){ 

                                mapLatLong.push({
                                    "id":key,
                                    "coordinates":result.electionResults.gpsPollingStation,
                                    "data":result.electionResults,
                                    "partyInfo":result.partyInfo,
                                    "roundCode": result.roundCode,
                                    "lists": elements,

                                })
                            }  
                        }
                    })
              
             }) 
        }
    }
  
  
    return { 
        mapResults: mapLatLong,
    };
};


export default connect(mapStateToProps)(MapResult);
